   $vr-font-size: 12px;
   $vr-modularscale: perfect-fifth;

@include vr-reset(
    $helpers: false,
    $font-size: 8px,
    $modular-scale: perfect-fifth,
    $breakpoints:(
    // 1em: ( // 400px
    //   font-size: 6px,
    //   line-height: 1.5,
    //   modular-scale: perfect-fifth,
    //   grid-color: purple
    // ),
    25em: ( // 400px
      font-size: 8px,
      line-height: 1.5,
      modular-scale: perfect-fifth,
      grid-color: purple
    ),
  30em: ( // 480px
      font-size: 8px,
      line-height: 1.5,
      modular-scale: perfect-fifth,
      grid-color: blue
    ),
  35em: ( // 560px
      font-size: 10px,
      line-height: 1.5,
      modular-scale: perfect-fifth,
      grid-color: green
    ),
    40em: ( // 640px
        font-size: 8px,
        line-height: 1.5,
        modular-scale: perfect-fifth,
        grid-color: yellow
    ),
    48em: ( // 768px
        font-size    : 8px,
        line-height  : 1.5,
        modular-scale: perfect-fifth,
        grid-color   : orange,
    ),
    64em: ( // 1024px
        font-size    :8px,
        line-height  : 1.5,
        modular-scale: perfect-fifth,
        grid-color   : purple,
    ),
    78em: ( // 1248px
        font-size    : 8px,
        line-height  : 1.5,
        modular-scale: perfect-fifth,
        grid-color   : blue,
    ),
    90em: ( // 1440px
        font-size    : 10,
        line-height  : 1.5,
        modular-scale: perfect-fifth,
        grid-color   : blue,
    ),
    118em: ( // 1920px
        font-size    : 10,
        line-height  : 1.5,
        modular-scale: perfect-fifth,
        grid-color   : purple,
    ),
    160rem: ( // 2560px
        font-size    : 16,
        line-height  : 1.5,
        modular-scale: perfect-fifth,
        grid-color   : blue,
    )
)
    );


html {
    height: 100%;
    scroll-behavior: smooth;
}

html [type=button] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

