.fixed-top{
    position: fixed;
    top: 0;
    z-index: 1000;
}

.container{
  position: relative;
}

%row, .row{
    margin-left: 0;
    margin-right: 0;
}

.wrapper{
  padding-top: 0;
  padding-bottom: 0;
  padding-left: 0;
  padding-right: 0;
}

.article__wrapper{
   margin-right: auto;
   margin-left: auto;
   position: relative;
   max-width: 100%;
   top: 0;
}

.article__wrapper-fluid{
    width: 100%;
    max-width: 100%;
    padding-left: 0;
    padding-right: 0;
    position: relative;
}





.content__container_row {
  display: flex;
  flex-direction: row;
}

.content__container_col {
  display: flex;
  flex-direction: column;
}

.content__item_col {
  display: flex;
  flex-direction: column;
}

.content__item_row {
  display: flex;
  flex-direction: row;
}

.content__block {
  width: 100%;
  max-width: 100%;
  flex: 0 0 auto;
}

